/* You can add global styles to this file, and also import other style files */

@import url("./reset.css");
@import url("./icons.css");

@font-face {
    font-family: "Gothic A1";
    font-weight: 400;
    src: url("./assets/fonts/gothic-a1/GothicA1-Regular.ttf");
}

@font-face {
    font-family: "Gothic A1";
    font-weight: 500;
    src: url("./assets/fonts/gothic-a1/GothicA1-Medium.ttf");
}

@font-face {
    font-family: "Gothic A1";
    font-weight: 600;
    src: url("./assets/fonts/gothic-a1/GothicA1-SemiBold.ttf");
}

@font-face {
    font-family: "Gothic A1";
    font-weight: 700;
    src: url("./assets/fonts/gothic-a1/GothicA1-Bold.ttf");
}

@font-face {
    font-family: "Roboto";
    font-weight: 700;
    src: url("./assets/fonts/roboto/Roboto-Bold.ttf");
}

:root {
    --c-dark-10: rgba(0, 0, 0, 0.1);
    --c-dark-20: rgba(0, 0, 0, 0.2);
    --c-dark-40: rgba(0, 0, 0, 0.4);
    --c-dark-60: rgba(0, 0, 0, 0.6);
    --c-dark-80: rgba(0, 0, 0, 0.8);
    --c-dark-100: #000000;
    --c-green-60: #49AA19;
    --c-primary-60: #4158B6;
    --c-primary-70: #2D4294;
    --c-primary-80: #1D2E72;
    --c-red-60: #D50000;
    --c-yellow-60: #F39C11;
    --c-white-10: #FFFFFF;
    --c-white-20: #F4F4F4;
    --c-white-30: #ECECEC;
}

* {
    box-sizing: border-box;
}

body, html {
    height: 100%;
    width: 100%;
}

body {
    background-color: var(--c-white-20);
    display: flex;
    font-family: "Gothic A1";
    justify-content: center;
    overflow: hidden;
}

app-root {
    display: contents;
}

.grid {
    align-content: flex-start;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    row-gap: 16px;
}

[class^="col-"] {
    padding-left: 8px;
    padding-right: 8px;
}

.col-1 {
    width: 12.5%;
}

.col-2 {
    width: 25%;
}

.col-3 {
    width: 37.5%;
}

.col-4 {
    width: 50%;
}

.col-5 {
    width: 62.5%;
}

.col-6 {
    width: 75%;
}

.col-7 {
    width: 87.5%;
}

.col-8 {
    width: 100%;
}

.text-preset-230 {
    font-size: 8px;
    font-weight: 600;
    line-height: 125%;
}

.text-preset-250 {
    font-size: 10px;
    font-weight: 700;
    line-height: 100%;
}

.text-preset-300 {
    font-size: 12px;
    font-weight: 400;
    line-height: 100%;
}

.text-preset-320 {
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
}

.text-preset-350 {
    font-size: 14px;
    font-weight: 400;
    line-height: 125%;
}

.text-preset-360 {
    font-size: 14px;
    font-weight: 400;
    line-height: 150%;
}

.text-preset-420 {
    font-size: 16px;
    font-weight: 500;
    line-height: 100%;
}

.text-preset-430 {
    font-size: 16px;
    font-weight: 600;
    line-height: 125%;
}

.text-preset-450 {
    font-size: 16px;
    font-weight: 400;
    line-height: 150%;
}

.text-preset-460 {
    font-size: 16px;
    font-weight: 500;
    line-height: 150%;
}

.text-preset-470 {
    font-size: 16px;
    font-weight: 700;
    line-height: 150%;
}

.text-preset-500 {
    font-size: 20px;
    font-weight: 400;
    line-height: 100%;
}

.text-preset-520 {
    font-size: 20px;
    font-weight: 500;
    line-height: 125%;
}

.text-preset-600 {
    font-size: 24px;
    font-weight: 400;
    line-height: 150%;
}

.text-preset-650 {
    font-size: 24px;
    font-weight: 700;
    line-height: 150%;
}

.table {
    width: 100%;
}

/* * = tbody/thead */
.table > * > tr {
    border-bottom: 1px solid var(--c-dark-20);
}

.table > * > tr:last-child {
    border: none;
}

.table > * > tr > td {
    color: var(--c-dark-60);
    padding: 24px 12px;
}